import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { LocalStorageServiceService } from '../../service/local-storage-service.service';
import { GetDataBackendService } from 'src/service/get-data-backend.service';
import { AlertController } from '@ionic/angular';

@Component({
  selector: 'app-Asignacion',
  templateUrl: './Asignacion.page.html',
  styleUrls: ['./Asignacion.page.scss'],
})
export class AsignacionPage implements OnInit {
  ButtonAtras: boolean = true;
  DatosAsign: any = {}
  dateValue: any
  DataUser: any
  DataJornada: any
  ListAsignacion: any = []
  ShowModal: Boolean = false
  TipoProceso: string



  @ViewChild('timeInicioValue') timeInicioValue: any
  @ViewChild('timeFinValue') timeFinValue: any

  constructor(
    private _localstorage: LocalStorageServiceService,
    private _GetData: GetDataBackendService,
    private alertController: AlertController,
    private ref: ChangeDetectorRef
  ) { }

  ngOnInit() {
    //asigno los datos del usuario que tengo en el local storage 
    this.DataUser = JSON.parse(this._localstorage.BuscarItem('DatosUser'))
    this.ObtenerJornadas()
  }

  ngAfterContentChecked() {
    this.ref.detectChanges();
  }

  ObtenerJornadas() {
    //busco las jornadas
    try {

      this._GetData.GetJornada(this.DataUser.idUser).then((Data: any) => {
        if (Data.length > 0) {
          this.ListAsignacion = Data
        }
      })

    } catch (error) {
      console.error(error)
    }
  }

  GuardarDatos() {

    //las horas seleccionadas las convierto en tipo fecha
    this.timeInicioValue = new Date(this.timeInicioValue)
    this.timeFinValue = new Date(this.timeFinValue)

    let HorasJornada: any = []

    let pos = 0
    //aqui le coloco los ceros que le falten a los minutos
    for (let index = this.timeInicioValue.getHours(); index < this.timeFinValue.getHours(); index++) {
      HorasJornada.push({
        id: pos,
        Hora: `${index}:${this.FormatMinutos(this.timeInicioValue.getMinutes())}`,
        status: true
      })
      pos = pos + 1
    }


    if (this.TipoProceso === 'Actualizar') {
//armo la estructura a editar
      this.DatosAsign = {
        idAsig: this.DataJornada.idAsig,
        idUser: this.DataJornada.idUser,
        Fecha: this.DataJornada.Fecha,
        Hora_In: `${this.timeInicioValue.getHours()}:${this.FormatMinutos(this.timeInicioValue.getMinutes())}`,
        Hora_Fin: `${this.timeFinValue.getHours()}:${this.FormatMinutos(this.timeFinValue.getMinutes())}`,
        HorasDisponible: HorasJornada
      }

      this._GetData.UpdateJornada(this.DatosAsign).then((ResUp: any) => {
//guarda los cambios a la joranada
        if (ResUp.status === true) {

          const PosAsign = this.ListAsignacion.indexOf((Asig: any) => Asig.idAsig === this.DatosAsign.idAsig)

          this.ListAsignacion[PosAsign] = this.DatosAsign

          this.ShowAlert('Edit')
          this.FinalizarProceso()

        } else {
          this.ShowAlert('EditError')
        }

      }).catch((err: any) => { console.error(err); this.ShowAlert('EditError') })

    } else {

      //aqui armo la estructura de una jornada nueva
      this.DatosAsign = {
        idAsig: Math.random().toString(36).substring(2, 8 + 2),
        idUser: this.DataUser.idUser,
        Fecha: new Date(this.dateValue),
        Hora_In: `${this.timeInicioValue.getHours()}:${this.FormatMinutos(this.timeInicioValue.getMinutes())}`,
        Hora_Fin: `${this.timeFinValue.getHours()}:${this.FormatMinutos(this.timeFinValue.getMinutes())}`,
        HorasDisponible: HorasJornada
      }

      this._GetData.InsertJornada(this.DatosAsign).then((Res: any) => {

        if (Res.status === true) {

          this.ListAsignacion.push(Res.mesage)

          this.ShowAlert('New')
          this.FinalizarProceso()
        } else {
          this.ShowAlert('NewError')
        }

      }).catch((err: any) => { console.error(err); this.ShowAlert('NewError') })

    }

  }

  ActualizarJornada(IdJornada: any) {
    this.TipoProceso = 'Actualizar'
    //Busco la jornada que se quiere actualizar dentro del arreglo de jornada 
    this.DataJornada = this.ListAsignacion.find((Asig: any) => Asig.idAsig === IdJornada)

    //asignar los datos de la jornada seleccionada
    this.dateValue = this.DataJornada.Fecha
  
    this.timeInicioValue = this.FormatHora(this.DataJornada.Hora_In)
    this.timeFinValue = this.FormatHora(this.DataJornada.Hora_Fin)

    this.ViewModal()

  }

  async EliminarJornada(IdJornada: any) {
    try {

      const alert = await this.alertController.create({
        header: '¿Usted desea eliminar esta Asignación?',
        buttons: [
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'secondary',
          },
          {
            text: 'Confirmar',
            handler: async (data) => {

              this._GetData.DeleteJornada(IdJornada).then((ResD: any) => {

                if (ResD.status && ResD.mesage != null || undefined) {
                  //filtro todas las jornadas sin incluir la que se elimino 
                  this.ListAsignacion = this.ListAsignacion.filter((Asig: any) => Asig.idAsig != IdJornada)
                  this.ShowAlert('Delete')
                } else {
                  this.ShowAlert('DeleteError')
                }

              }).catch((err: any) => { console.error(err); this.ShowAlert('DeleteError') })

            }
          }
        ]
      });

      await alert.present();

    } catch (error) {
      console.error(error)
    }
  }

  FormatHora(Fecha: any) {
    //aqui se convierten los datos en tipo fecha
    let FechaA = new Date()
    let Hora, Minuto
    const Pos = Fecha.indexOf(':')

    if (Pos > 0) {
      Hora = Fecha.substring(0, Pos)
      Minuto = Fecha.substring(Pos + 1, Fecha.length)
      FechaA.setHours(parseInt(Hora))
      FechaA.setMinutes(parseInt(Minuto))
      return FechaA
    }

  }

  FormatMinutos(Minutos: any) {
    //aqui le agrego los ceros a los minutos
    Minutos = Minutos.toString()
    if (Minutos.length === 1) {
      return `0${Minutos}`
    } else { return Minutos }
  }

  ViewModal() {
    //mostrar u ocultar el modal del formulario
    if (this.ShowModal) {
      this.ShowModal = false
    } else if (!this.ShowModal) {
      this.ShowModal = true
    }
  }

  FinalizarProceso() {
    this.ViewModal()
    this.TipoProceso = ''
    this.dateValue = ''
    this.timeInicioValue = ''
    this.timeFinValue = ''
    this.DatosAsign = {}
  }

  async ShowAlert(TipoAlert: String) {
    try {

      const alert = await this.alertController.create({
        header: 'Asignación agregada con éxito',
        buttons: ['Aceptar']
      });

      const alertError = await this.alertController.create({
        header: '¡Hubo un error al guardar su asignación!',
        buttons: ['Aceptar']
      });

      const alertUpdate = await this.alertController.create({
        header: 'Se actualizo la asignación con éxito',
        buttons: ['Aceptar']
      });

      const alertErrorUpdate = await this.alertController.create({
        header: '¡Hubo un error al actualizar la asignación!',
        buttons: ['Aceptar']
      });

      const alertDelete = await this.alertController.create({
        header: 'La asignación se elimino con éxito',
        buttons: ['Aceptar']
      });

      const alertErrorDelete = await this.alertController.create({
        header: '¡Hubo un error al eliminar la asignación!',
        buttons: ['Aceptar']
      });

      switch (TipoAlert) {
        case 'New':
          await alert.present();
          break;
        case 'NewError':
          await alertError.present();
          break;
        case 'Edit':
          await alertUpdate.present();
          break;
        case 'EditError':
          await alertErrorUpdate.present();
          break;
        case 'Delete':
          await alertDelete.present();
          break;
        case 'DeleteError':
          await alertErrorDelete.present();
          break;
      }

    } catch (error) {
      console.error(error)
    }
  }

}
